<template>
    <div class="search">
        <router-link to="/">
            <img :src="require('@/assets/logo.jpg')" class="home">
        </router-link>
        <input type="text" id="schT" placeholder="검색" v-model="srchText" @keyup.enter="search">
        <button @click="menuClick" class="btn">메뉴</button>
        <div v-if="showMenu" class="menu">
          <PageAside/>
        </div>
    </div>
</template>

<script>
import PageAside from '@/components/PageAside.vue'

export default {
  components: {
    PageAside
  },
  data () {
    return {
      srchText: '',
      showMenu: false
    }
  },
  methods: {
    search () {
      if (this.srchText === '') { this.$router.replace(this.$router.path) } else { this.$router.push('/search') }
      this.$store.commit('setSearchText', this.srchText)
      this.srchText = ''
    },
    menuClick () {
      if (this.showMenu === false) { this.showMenu = true } else { this.showMenu = false }
    }
  }
}
</script>

<style>
    .btn{
      margin:10px;
      padding:10px;
      background-color: white;
    }
    .menu{
      position:fixed;
      background-color: white;
      animation: fadeIn 1s;
    }
    .home:hover{
      border:2px solid lightgrey;
      border-radius: 20%;
      padding:8.5px;
    }
    .home:active{
      background-color:lightgrey;
    }
    .search{
        position: sticky;
        top : 0px;

        display:flex;
        align-items:center;
        background-color: white;
        height: 50px;
    }
    .search img{
        padding : 10px;
        width:60px;
        height: 35px;
        margin-top:5px;
        margin-right:10px;
    }
    #schT{
        margin:0;
        width:300px;
        height:35px;
        border-radius: 50px;
        font-size:15px;
        padding-left:10px;
        border:2px solid grey;
        font-weight: bold;
    }
    #schT:focus{
      border:4px solid lightblue;
      box-shadow:0 0 10px lightgrey;
    }
    #schT:hover{
      background-color: rgb(235, 235, 235);
    }

</style>
