<template>
  <div>
    <PageHeader/>
    <br>
    <div class = "picspace">
      <div class = "container">
        <figure v-for="arch in archList" :key="arch" class = "pic" @click="openModal(arch)">
            <img :src="require(`@/assets/${arch}`)" alt="풍경사진">
            <figcaption>{{getName(arch, 'arch')}}</figcaption>
        </figure>
        <figure v-for="obj in objList" :key="obj" class = "pic" @click="openModal(obj)">
            <img :src="require(`@/assets/${obj}`)" alt="풍경사진">
            <figcaption>{{getName(obj, 'obj')}}</figcaption>
        </figure>
        <figure v-for="scene in sceneList" :key="scene" class = "pic" @click="openModal(scene)">
            <img :src="require(`@/assets/${scene}`)" alt="풍경사진">
            <figcaption>{{getName(scene, 'scene')}}</figcaption>
        </figure>
      </div>
      <div v-if="showModal" class="modal">
        <div class="modal-content">
          <span class="close" @click="closeModal">&times;</span>
          <img :src="modalImage" alt="풍경사진" class="modal-image">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PageHeader from '@/components/PageHeader.vue'

export default {
  name: 'Main',
  data () {
    return {
      showModal: false,
      modelImage: ''
    }
  },
  components: {
    PageHeader
  },
  computed: {
    archList () {
      return this.$store.state.archList.map(item => item.url)
    },
    objList () {
      return this.$store.state.objList.map(item => item.url)
    },
    sceneList () {
      return this.$store.state.sceneList.map(item => item.url)
    }
  },
  methods: {
    getName (text, type) {
      switch (type) {
        case 'arch':
          return this.$store.state.archList.find(item => item.url === text)?.name || ''
        case 'obj' :
          return this.$store.state.objList.find(item => item.url === text)?.name || ''
        case 'scene' :
          return this.$store.state.sceneList.find(item => item.url === text)?.name || ''
      }
    },
    openModal (image) {
      console.log(image)
      this.modalImage = require(`@/assets/${image}`)
      this.showModal = true
    },
    closeModal () {
      this.showModal = false
    }
  }
}
</script>
